#check {
  display: none;
}

.thumb {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 20px;
  background-color: hsl(225, 20%, 60%);
  border-radius: 30px;
}
.thumb:before {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  background-color: white;
  top: 3px;
  left: 5px;
  border-radius: 50px;
  transition: 0.4s;
}

input:checked + .thumb:before {
  transform: translateX(16px);
}

@media (max-width: 900px) {
  .thumb {
    display: inline-block;
    position: relative;
    top: -4px;
    width: 30px;
    height: 18px;
    background-color: hsl(225, 20%, 60%);
    border-radius: 30px;
    margin: 0px 6px;
  }
  .thumb:before {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: white;
    top: 4px;
    left: 2px;
    border-radius: 50px;
    transition: 0.4s;
  }

  input:checked + .thumb:before {
    transform: translateX(16px);
  }
}
