.background {
  position: relative;
  height: 100vh;
  width: 100%;
}

.heading {
  z-index: 1;
  position: absolute;
  top: 14%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
}

.heading-main {
  color: hsl(227, 35%, 25%);

  font-size: 2rem;
  font-weight: 800;
  letter-spacing: 0.1rem;
  display: block;
}

.heading-sub {
  color: hsl(225, 20%, 60%);
  margin-top: 5px;

  font-size: 1rem;
  font-weight: 100;
  letter-spacing: 0.1rem;

  display: block;
}

.main {
  position: absolute;
  box-shadow: 0 12px 16px rgba(0, 0, 0, 0.07);
  background-color: white;
  padding: 25px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 600px;
  height: 400px;
  display: grid;
  grid-row-gap: 10px;
  grid-template-rows: 25% 1% 25% 1fr;
  grid-template-columns: 1fr 1fr;
}
.imgDiv {
  z-index: -1;
  position: absolute;
  top: 6%;
  left: 45%;
  height: 145px;
  width: 146px;
  background-repeat: no-repeat;
  background-image: url("./assets/images/pattern-circles.svg");
}
.upperStrip {
  line-height: 5rem;
  grid-row: 1/2;
  grid-column: 1/-1;
}
.pro-slider {
  backface-visibility: hidden;
  -webkit-appearance: none;
  width: 100%;
  height: 10px;
  background: #26ac8a;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.pro-slider:hover {
  opacity: 1;
}

.pro-slider::-webkit-slider-thumb {
  position: relative;
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  background-position: center;
  background-image: url("./assets/images/icon-slider.svg");
  background-repeat: no-repeat;
  background-color: rgb(100, 184, 149);
  box-shadow: 0px 8px 20px rgb(100, 184, 149);
}
.sliderCont {
  justify-self: center;
  display: inline-block;
  align-self: center;
  width: 80%;
  grid-row: 2/3;
  grid-column: 1/-1;
}
.lowerStrip {
  line-height: 4rem;
  position: relative;
  align-self: center;
  justify-self: center;
  grid-row: 3/4;
  grid-column: 1/-1;
}
.list {
  grid-row: 4/5;
  grid-column: 1/2;
}
.startBtn {
  grid-row: 4/5;
  grid-column: 2/3;
}
.upperStrip {
  align-self: center;
}

.views {
  color: hsl(225, 20%, 60%);
  margin-left: 20px;
  font-size: 0.9rem;
  margin-left: 2rem;
  font-weight: 600;
  letter-spacing: 0.1rem;
}
.tinyText {
  color: hsl(225, 20%, 60%);
  margin-left: 20px;
  font-size: 0.9rem;
  font-weight: 600;
}
.r1 {
  color: hsl(225, 20%, 60%);
  font-size: 0.9rem;
  font-weight: 600;
  float: right;
}
.price {
  color: hsl(227, 35%, 25%);
  font-size: 2rem;
}

.switch {
  position: relative;
  top: 7px;
  left: 13px;
}

.discountText {
  background-color: hsl(14, 92%, 95%);
  position: relative;
  top: 2px;
  margin-left: 10px;
  color: hsl(15, 100%, 70%);
}

.list {
  list-style-image: url("./assets/images/icon-check.svg");
  position: relative;
  top: 20px;
  left: 20px;
  color: hsl(225, 20%, 60%);
}

.startBtn {
  outline: none;

  align-self: center;
  justify-self: center;
  color: white;
  background-color: hsl(227, 35%, 25%);
  width: 45%;
  height: 23%;
  border-radius: 50px;
  cursor: pointer;
}

li {
  margin: 10px;
}

@media (max-width: 900px) {
  .heading {
    z-index: 1;
    position: absolute;
    top: 15%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
  }
  .heading-main {
    color: hsl(227, 35%, 25%);
    font-size: 1.3rem;
    font-weight: 800;
    display: block;
  }
  .heading-sub {
    color: hsl(225, 20%, 60%);
    margin-top: 5px;
    font-size: 0.8rem;
    font-weight: 100;
    letter-spacing: 0.1rem;
    display: block;
  }
  .imgDiv {
    z-index: -1;
    position: absolute;
    top: 15%;
    left: 50%;
    height: 145px;
    width: 146px;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-image: url("./assets/images/pattern-circles.svg");
  }
  .main {
    position: absolute;
    box-shadow: 0 12px 16px rgba(0, 0, 0, 0.07);
    background-color: white;
    padding: 5px;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    height: 400px;
    display: flex;
    flex-direction: column;
    grid-row-gap: 10px;
    grid-template-rows: 25% 1% 25% 1fr;
    grid-template-columns: 1fr 1fr;
  }
  .upperStrip {
    text-align: center;
    line-height: 2rem;
    flex-basis: 100%;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    height: 10%;
    max-height: 25%;
  }
  .views {
    color: hsl(225, 20%, 60%);
    font-size: 0.8rem;
    font-weight: 600;
    letter-spacing: 0.2rem;
  }
  .tinyText {
    color: hsl(225, 20%, 60%);
    font-size: 0.9rem;
    font-weight: 400;
  }
  .r1 {
    color: hsl(225, 20%, 60%);
    font-size: 0.9rem;
    font-weight: 600;
    float: right;
  }
  .lowerStrip {
    display: flex;
    flex-direction: row;
    line-height: 1rem;
    flex-basis: 20%;
    position: relative;
    line-height: 1rem;
    justify-content: space-around;
  }
  .discountText {
    content: "-25%";
  }
  .sliderCont {
    margin-bottom: 23px;
  }
  .month,
  .r31 {
    text-align: left;
    justify-content: flex-start;
    padding: 0;
    margin: 0;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
  }
  .lowerStrip {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 0;
    margin: 20px 0px;
    line-height: 10px;
    max-height: 15px;
  }
  .switch {
    position: static;
    top: -4px;
    left: 0;
  }
  .list {
    list-style-image: url("./assets/images/icon-check.svg");
    position: relative;
    top: 0px;
    left: 20px;
    color: hsl(225, 20%, 60%);
    margin: 10px auto;
  }
  li {
    margin: 7px;
  }
  .startBtn {
    outline: none;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    color: white;
    background-color: hsl(227, 35%, 25%);
    width: 30%;
    height: 10%;
    border-radius: 30px;
  }
}
